import React from "react";
import { makeStyles } from "@material-ui/core/styles";

import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import GridContainer from "components/Grid/GridContainer.jsx";
import GridItem from "components/Grid/GridItem.jsx";
import backend from "assets/img/logo/backend.png";

import dotnet from "assets/img/logo/.net.png";
import javascript from "assets/img/logo/javascript.png";
import csharp from "assets/img/logo/csharp.png";
import nodejs from "assets/img/logo/nodejs.png";
import CloseIcon from "@material-ui/icons/Close";

const useStyles = makeStyles(theme => ({
  modal: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center"
  },
  paper: {
    backgroundColor: theme.palette.background.paper,
    border: "2px solid #000",
    boxShadow: theme.shadows[5],
    padding: theme.spacing(80, 160, 120)
  },
  imgFluid: {
    maxWidth: "100%",
    height: "auto"
  },
  imgRounded: {
    borderRadius: "6px !important"
  },
  imgRoundedCircle: {
    borderRadius: "50% !important"
  },
  imgRaised: {
    boxShadow:
      "0 5px 15px -8px rgba(0, 0, 0, 0.24), 0 8px 10px -5px rgba(0, 0, 0, 0.2)"
  }
}));

export default function Backend() {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <>
      <Button onClick={handleOpen} color="primary">
        <img
          src={backend}
          alt="..."
          className={
            classes.imgRaised +
            " " +
            classes.imgRounded +
            " " +
            classes.imgFluid
          }
        />
      </Button>
      <Dialog
        scroll="body"
        maxWidth="xl"
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <GridContainer>
          <GridItem xs={12} sm={6}>
            <DialogTitle id="alert-dialog-title">
              <b>Web - Back-end</b>
            </DialogTitle>
          </GridItem>

          <GridItem xs={12} sm={6}>
            <DialogActions>
              <Button onClick={handleClose} color="secondary" label="Agree">
                <CloseIcon className={classes.icons} />
              </Button>
            </DialogActions>
          </GridItem>
        </GridContainer>
        <DialogContent>
          <GridContainer xs={12} sm={6}>
            <GridItem xs={12} sm={1}>
              <img
                src={csharp}
                alt="..."
                title="c#"
                className={
                  classes.imgRaised +
                  " " +
                  classes.imgRounded +
                  " " +
                  classes.imgFluid
                }
              />
            </GridItem>
            <GridItem xs={12} sm={1}>
              <img
                src={dotnet}
                alt="..."
                title=".Net Core"
                className={
                  classes.imgRaised +
                  " " +
                  classes.imgRounded +
                  " " +
                  classes.imgFluid
                }
              />
            </GridItem>
            <GridItem xs={12} sm={1}>
              <img
                src={javascript}
                alt="..."
                title="Javascript"
                className={
                  classes.imgRaised +
                  " " +
                  classes.imgRounded +
                  " " +
                  classes.imgFluid
                }
              />
            </GridItem>
            <GridItem xs={12} sm={1}>
              <img
                src={nodejs}
                alt="..."
                title="Node.js"
                className={
                  classes.imgRaised +
                  " " +
                  classes.imgRounded +
                  " " +
                  classes.imgFluid
                }
              />
            </GridItem>
          </GridContainer>

          <DialogTitle id="alert-dialog-title">Lien avec projets :</DialogTitle>
          <DialogContentText id="alert-dialog-description">
            <ul>
              <li>
                <a href="#coursdescomptes">Falcon</a>
              </li>
              <li>
                <a href="unhaj">Unhaj</a>
              </li>
              <li>
                <a href="falconlabel">Mobiteach</a>
              </li>
            </ul>
          </DialogContentText>
          <DialogTitle id="alert-dialog-title">
            Lien avec comptences humaines :
          </DialogTitle>

          <DialogContentText id="alert-dialog-description">
            <ul>
              <li>
                <a href="#curiosite" onClick={handleClose}>
                  Curiosit
                </a>
              </li>
            </ul>
          </DialogContentText>
        </DialogContent>
        <DialogContent>
          <DialogTitle id="alert-dialog-title">Presentation : </DialogTitle>
          <DialogContentText>
            Le Back-end (ou plus prcisment Api ou serveur) est le cur des
            applications.
            <br /> c'est lui qui est charg de fournir les donnes au diffrents
            services consommateurs (site internet, application mobile etc...).
            cette partie de notre mtier de dveloppeur ncessite une bonne
            connaissance des bases en dveloppement informatiques afin de faire
            des programmes rapide, modulaires et qui peuvent facilement voluer
            dans le temps.
          </DialogContentText>
          <DialogTitle id="alert-dialog-title">
            Le dveloppement Back-end, un jeu...
          </DialogTitle>
          <DialogContentText id="alert-dialog-description">
            C'est l'un des aspects de l'informatique qui m'a toujours attirs.
            mettre en place l'api la plus rapide. la plus conome en ressource
            la plus modulaire possible tout en rpondant aux besoins exprim par
            un client sont des problmatiques qui peuvent s'apparenter  celles
            du sport ou du jeu vido de mon point de vue, au niveau de la
            performance et de l'hygine ( de travail ou sportive).
          </DialogContentText>

          <DialogTitle id="alert-dialog-title">
            ... qui devient un mtier
          </DialogTitle>
          <DialogContentText id="alert-dialog-description"></DialogContentText>
          <DialogTitle id="alert-dialog-title">Projection : </DialogTitle>
          <DialogContentText id="alert-dialog-description">
            Au fur et  mesure de mes expriences avec le dveloppement
            informatique j'ai eu la chance d'tre confront  plusieurs
            problmatiques, la rsolution de celle-ci demande de se documenter
            et rechercher les informations. Lors de mes projets professionnels
            en .net Core et C#, j'ai aussi t sensibilis  l'importance
            crucial de l'architecture de mes applications. Il fallait donc tre
            performant oui, mais aussi faire preuve d'une hygine de code
            irrprochable.
          </DialogContentText>

          <h3 id="alert-dialog-title">Projection : </h3>
          <DialogContentText id="alert-dialog-description">
            Je souhaite  l'avenir renforcez mes comptences en architecture de
            logiciel.Notamment sur les architectures distribues
          </DialogContentText>
        </DialogContent>
      </Dialog>
    </>
  );
}
